import React from "react"
import { Link } from "gatsby"
import "./model-nav.css"

class ModelNav extends React.Component {
  constructor(props) {
    super(props)
    this.state = { isToggleOn: true }
    this.handleClick = this.handleClick.bind(this)
  }

  handleClick() {
    this.setState(state => ({
      isToggleOn: !state.isToggleOn,
    }))
  }

  render() {
    let action = this.state.isToggleOn ? " ↑ " : " ↓ "

    return (
      <div className="modelNavigatorWrapper">
        <div className="action" onClick={this.handleClick}>
          {action}
        </div>
        <div
          className={"modelNavigator " + (this.state.isToggleOn ? "" : "none")}
        >
          <p>Однострочные</p>
          <Link to="/">Вариант 1 (базовый)</Link>
          <Link to="/variant-2/">Вариант 2 (adry)</Link>
          <Link to="/variant-3/">Вариант 3 (Silvia)</Link>
          <Link to="/grid/">Модель сетки</Link>
          <p>Двустрочные</p>
          <Link to="/index-2/">Вариант 1 (базовый)</Link>
          <Link to="/variant-22/">Вариант 2 (adry)</Link>
          <Link to="/variant-32/">Вариант 3 (Silvia)</Link>
          {/* <a href="http://bet2.adry.ru/">История купонов</a> */}
        </div>
      </div>
    )
  }
}

export { ModelNav }
